<template>
    <div class="list" v-if="!is_loading && !is_search">
        <ul class="pokedex__list">
            <li class="pokedex__list-item" v-for="(pokemon, index) in pokemons"
                :key="index"
            >
                <span class="pokemon-id">
                    {{ pretty_id(index + 1) }}
                </span>
                <div class="pokemon-image__container">
                    <div class="pokeball"></div>
                    <router-link :to="`/pokemon-detail/${index + 1}`">
                        <img class="pokemon-image" :src="image_url + pokemon.name + '.png'" :alt="pokemon.name">
                    </router-link>
                </div>
                <span class="pokemon-name">
                    {{ pokemon.name }}
                </span>
            </li>
        </ul>
    </div>

    <div class="list" v-else-if="!is_loading && is_search">
        <ul class="pokedex__list d-flex justify-content-center align-items-center">
            <li class="pokedex__list-item w-50" v-for="(value, index) in search"
                :key="index"
            >
                <span class="pokemon-id">
                    {{ pretty_id(value.id) }}
                </span>
                <div class="pokemon-image__container">
                    <div class="pokeball"></div>
                    <router-link :to="`/pokemon-detail/${value.id}`">
                        <img class="pokemon-image" :src="image_url + value.name + '.png'" :alt="value.name">
                    </router-link>
                </div>
                <span class="pokemon-name">
                    {{ value.name }}
                </span>
            </li>
        </ul>
    </div>
</template>

<script>
    import $ from 'jquery'
    export default {
        props: [
            'image_url',
            'api_url',
        ],
        data() {
            return {
                pokemons: [],
                next_url: '',
                is_loading: true,
                is_search: false,
                search: [],
            }
        },
        methods: {
            // Fetch Data from API
            fetch_data(url) {
                this.axios.get(url)
                .then((response) => {
                    if(response.status === 200) {
                        this.next_url = response.data.next;
                        response.data.results.forEach((pokemon) => {
                            this.pokemons.push(pokemon);
                        });
                    }
                })
                .finally(() => {
                    this.is_loading = false;
                })
                .then(() =>  this.infinite_scroll())
            },

            // Next URL
            next() {
                this.fetch_data(this.next_url);
            },

            pretty_id(id) {
                id = id.toString();

                id = id.padStart(3, '0');

                id = '#' + id;

                return id
            },

            infinite_scroll() {
                const pokemon = $('.pokedex__list-item');
                const observer = new IntersectionObserver((entries) => {
                    entries.forEach(entry => {
                        entry.target.classList.toggle('show', entry.isIntersecting);
                    });
                }, {
                    threshold: 0.5,
                });

                const last_child_observer = new IntersectionObserver((entries) => {
                    const last_child = entries[0];

                    if(!last_child.isIntersecting) {
                        return;
                    }
                    
                    if(!this.next_url) {
                        return;
                    }

                    this.next();

                    last_child_observer.unobserve(last_child.target);
                }, {
                    rootMargin: '100px',
                });

                if(!this.is_loading && !this.is_search) {
                    last_child_observer.observe(document.querySelector(".pokedex__list-item:last-child"));
                }

                for(const each of pokemon) {
                    observer.observe(each);
                }
            },

            search_pokemon() {
                // Get Pokemon name from 'set_name' event
                this.$bus.on('set_name', value => {
                    this.search = [];
                    this.is_search = true;
                    this.is_loading = true;

                    if(value.toLowerCase() === 'zamazenta' || value.toLowerCase() === 'zacian') {
                        value = value.toLowerCase() + '-hero';
                    }

                    const url = `${this.api_url}${value.toLowerCase()}`;

                    this.fetch_by_name(url);
                    setTimeout(() => this.is_search = false, 5000);
                })
            },

            fetch_by_name(url) {
                this.axios.get(url)
                .then(response => {
                    if(response.status === 200) {
                        this.search.push(response.data);
                    }
                })
                .finally(() => {
                    this.is_loading = false;
                })
                .then(() => this.infinite_scroll());
            }
        },
        mounted() {
            this.fetch_data(this.api_url);
            this.search_pokemon();
        },
        updated() {
            this.infinite_scroll();
        },
    }
</script>

<style lang="scss" scoped>
    .list {
        margin-top: 5rem;
    }

    .pokedex__list {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(25rem, 1fr));
        grid-gap: 3rem;
        width: 100%;
        padding: 3rem;
        margin-bottom: 0;

        &-item {
            list-style: none;
            display: flex;
            justify-content: space-around;
            align-items: center;
            flex-direction: column;
            gap: 1rem;
            background-color: #B6CCFE;
            height: 25rem;
            text-align: center;
            border-radius: 1.2rem;
            cursor: pointer;
            box-shadow: 0.5rem 0.5rem 1rem rgba(64, 116, 238, 0.25),
                        -0.5rem -0.5rem 1rem rgba(64, 116, 238, 0.15),
                        inset -0.5rem -0.5rem 1rem rgba(64, 116, 238, 0.25),
                        inset 0.5rem 0.5rem 1rem rgba(64, 116, 238, 0.1);
            opacity: 0;
            visibility: hidden;
            transition: all 0.5s cubic-bezier(.6,.11,.73,.78);
            transform: scale(0);

            // PokéBall
            .pokemon-image__container {
                width: 10rem;
                height: 10rem;
                background-color: #EDF2FB;
                border-radius: 50%;
                border: 0.1rem solid #000000;
                overflow: hidden;
                position: relative;
                box-shadow: 0.5rem 0.5rem 1.5rem rgba(0, 0, 0, 0.12),
                            -0.5rem 0.5rem 1.5rem rgba(0, 0, 0, 0.12),
                            inset 0.3rem 0.3rem 1rem rgba(0, 0, 0, 0.15),
                            inset -0.3rem -0.3rem 1rem rgba(0, 0, 0, 0.15),
                            inset 0.5rem -0.5rem 1rem rgba(0, 0, 0, 0.2);

                &::before,
                &::after {
                    content: '';
                    position: absolute;
                }

                &::before {
                    background: linear-gradient(to top, transparent 50%, red 50%);
                    top: -20%;
                    left: -20%;
                    width: 120%;
                    height: 120%;
                    border-radius: 50%;
                    transform: translate(10%, 10%);
                }

                &::after {
                    top: 50%;
                    left: 0;
                    width: 120%;
                    height: 1rem;
                    background-color: #000000;
                    transform: translateX(-6%);
                }

                img.pokemon-image {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    z-index: 10;
                    filter: saturate(150%);
                }

                div.pokeball {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    width: 3rem;
                    height: 3rem;
                    background-color: #B6CCFE;
                    transform: translate(-50%, -35%);
                    border-radius: 50%;
                    border: 0.2rem solid #000000;
                    z-index: 5;
                    box-shadow: inset 0.1rem 0.1rem 0.3rem rgba(0, 0, 0, 0.1),
                                inset -0.1rem -0.1rem 0.3rem rgba(0, 0, 0, 0.1),
                                0.1rem 0.1rem 0.3rem rgba(0, 0, 0, 0.1);

                    &::before {
                        position: absolute;
                        content: '';
                        top: 50%;
                        left: 50%;
                        width: 60%;
                        height: 60%;
                        background-color: #EDF2FB;
                        transform: translate(-50%, -50%);
                        border-radius: 50%;
                        z-index: 6;
                        box-shadow: 0.2rem 0.2rem 0.5rem rgba(0, 0, 0, 0.12),
                                    -0.2rem -0.2rem 0.5rem rgba(0, 0, 0, 0.12);
                    }
                }
            }

            span {
                text-transform: capitalize;
                background-color: #EDF2FB;
                width: 100%;
                height: 3rem;
                box-shadow: inset 0 0.2rem 0.4rem rgba(0, 0, 0, 0.12),
                            inset 0rem -0.2rem 0.4rem rgba(0, 0, 0, 0.12);
                border-top: 0.2rem solid rgba(64, 116, 238, 0.15);
                border-bottom: 0.2rem solid rgba(64, 116, 238, 0.15);
            }
        }

        &-item.show {
            opacity: 1;
            visibility: visible;
            transform: scale(1);
        }
    }
</style>